import React, { useState } from "react";
import { Link } from "react-router-dom";
const CreateUser = ({ createNewUser, setAlert }) => {
  const [newUserData, setnewUserData] = useState({
    name: "",
    accNum: "200",
    email: "123@gmail.com",
    balance: "2000",
  });
  const { name, accNum, email, balance } = newUserData;
  const onSubmit = (e) => {
    e.preventDefault();
    createNewUser(name, accNum, email, balance);
    setAlert("User created Successfully!!", "info");
  };
  const onChange = (e) => {
    e.preventDefault();
    setnewUserData({ ...newUserData, [e.target.name]: e.target.value });
  };
  return (
    <div className="container">
      <form onSubmit={(event) => onSubmit(event)}>
        <div className="form-group">
          <label htmlFor="name">Name: </label>
          <input
            className="form-control"
            type="text"
            name="name"
            value={name}
            required
            onChange={(event) => onChange(event)}
          />
        </div>
        <div className="form-group">
          <label htmlFor="email">Email:</label>
          <input
            className="form-control"
            type="email"
            name="email"
            value={email}
            onChange={(event) => onChange(event)}
            required
          />
        </div>
        <div className="form-group">
          <label htmlFor="accNum">Account Number:</label>
          <input
            className="form-control"
            type="text"
            name="accNum"
            value={accNum}
            onChange={(event) => onChange(event)}
            required
          />
        </div>
        <div className="form-group">
          <label htmlFor="balance">Amount</label>
          <input
            className="form-control"
            type="text"
            name="balance"
            value={balance}
            onChange={(event) => onChange(event)}
            required
          />
        </div>
        <button type="submit" className="btn btn-primary">
          Create
        </button>
        <div style={{ textAlign: "center" }}>
          <Link to="/" className="btn">
            <button className="btn btn-outline-danger">Home</button>
          </Link>
          <Link to="/users" className="btn">
            <button className="btn btn-outline-success">Show Users</button>
          </Link>
        </div>
      </form>
    </div>
  );
};

export default CreateUser;
