import React from "react";
import { Link } from "react-router-dom";
import "../../User.css";
const HomePage = () => {
  return (
    <div className="justify-content-center">
      <div className="jumbotron jumbotron-fluid">
        <div className="container center jumbo">
          <h1>Credit Management App!</h1>
          <p className="lead">
            This is a simple hero unit, a simple jumbotron-style component for
            calling extra attention to featured content or information.
          </p>
          <hr className="my-4" />
        </div>
      </div>
      <div
        style={{
          margin: "auto",
          marginTop: "60px",
          verticalAlign: "center",
          textAlign: "center",
        }}
      >
        <Link to="/createUser" className="btn">
          <button className="btn btn-outline-dark btn-lg">Add User</button>
        </Link>
        <Link to="/users" className="btn">
          <button className="btn btn-outline-primary btn-lg">Show Users</button>
        </Link>
        <Link to="/transfer" className="btn-inline">
          <button className="btn btn-outline-success btn-lg ">
            Transfer Amount
          </button>
        </Link>
      </div>
    </div>
  );
};

export default HomePage;
