import React from "react";
import account from "./account.jpg";
import { Link } from "react-router-dom";
import "../User.css";

const User = (props) => {
  const { name, email, creditAmount, accNumber } = props.location.user;

  return (
    <>
      <div className="container1">
        <div>
          <img src={account} className="image" alt="account" />
        </div>
        <div className="data">
          <p>Your Name: {name}</p>
          <p>
            <b>Acc: </b>
            {accNumber}
          </p>
          <p>Your email: {email}</p>
          <p>Your Current Credit: {creditAmount}</p>

          <Link to="/users">
            <button className="btn btn-primary" name="home">
              Back
            </button>
          </Link>

          <Link to="/transfer" className="btn">
            <button className="btn btn-primary" name="transfer">
              Transfer
            </button>
          </Link>
        </div>
      </div>
    </>
  );
};

export default User;
