import React from "react";
import "../User.css";
import { useHistory } from "react-router-dom";

const UsersList = (props) => {
  const history = useHistory();
  const users = props.users;
  const onClick = (event, user) => {
    event.preventDefault();
    history.push({
      pathname: "/user",
      user: user,
    });
  };
  return (
    <div className="container">
      <table className="table table-hover table-borderless table-responsiv-lg">
        <thead>
          <tr>
            <th scope="col">S.No</th>
            <th scope="col">Name</th>
            <th scope="col">Acc No.</th>
            <th scope="col">Balance</th>
          </tr>
        </thead>
        {users.map((user, key) => (
          <tbody key={key}>
            <tr onClick={(event) => onClick(event, user)}>
              <th scope="row">{key + 1}</th>
              <td>{user.name}</td>
              <td>{user.accNumber}</td>
              <td>{user.creditAmount}</td>
            </tr>
          </tbody>
        ))}
      </table>
      {/* <div className="row justify-content-around">
        {users.map((user, key) => (
          <div className="col-auto mb-5" key={key}>
            <div className="card card-container">
              <img
                className="card-img-top"
                src={account}
                alt="Card"
                style={{
                  width: "2rem",
                  height: "2rem",
                  margin: "auto",
                  marginTop: "10px",
                  border: "none",
                }}
              />
              <div className="card-body">
                <h5 className="card-title">{user.name}</h5>

                <Link to={{ pathname: "/user", user: user }} key={key}>
                  <button
                    type="button"
                    style={{
                      backgroundImage: "linear-gradient(to right, pink, teal)",
                      border: "0",
                      color: "white",
                    }}
                  >
                    {user.name}
                  </button>
                </Link>
              </div>
            </div>
          </div>
        ))}
      </div> */}
    </div>
  );
};

export default UsersList;
