import React from "react";
import { useState } from "react";
import { Link } from "react-router-dom";
import axios from "axios";
import Alert from "./layout/Alert";

const Transfer = ({ setAlert }) => {
  const [transferData, setTransferData] = useState({
    fromAcc: "100034",
    toAcc: "100",
    amount: "500",
  });
  const [confirmAcc, setConfirmAcc] = useState("");
  const [user, setUser] = useState({});
  const [loading, setloading] = useState(false);
  const { fromAcc, toAcc, amount } = transferData;
  const transferAmount = async () => {
    setloading(true);
    const res = await axios({
      method: "post",
      url: "/user/transfer",
      data: {
        fromAcc,
        toAcc,
        amount: Number(amount),
      },
      headers: { "Content-Type": "application/json" },
    });
    setloading(false);
    if (res.data === "Success") {
      setAlert(
        "Successfully transferred from " + fromAcc + " to " + toAcc,
        "success"
      );
    } else {
      setAlert(res.data, "danger");
    }
  };
  const alertMsg = {
    type: "light",
    msg: "Loading...",
  };
  const getUser = async (accNumber) => {
    const res = await axios.get(`/user/${accNumber}`);
    setUser(res.data[0]);
  };

  const onSubmit = (event) => {
    event.preventDefault();
    if (fromAcc === confirmAcc && fromAcc !== toAcc) {
      getUser(fromAcc);
    }
    if (fromAcc === toAcc) {
      setAlert("Sender and Recepient account numbers cannot be same", "danger");
    } else if (fromAcc !== confirmAcc) {
      setAlert("Account Numbers do not match!!", "danger");
    } else if (amount > user.creditAmount) {
      setAlert("Insufficient funds!!", "danger");
    } else {
      transferAmount();
    }
  };
  const onChange = (e) => {
    if (e.target.name === "fromAcc") {
      getUser(e.target.value);
    }
    setTransferData({ ...transferData, [e.target.name]: e.target.value });
    if (e.target.name === "confirmAcc") setConfirmAcc(e.target.value);
  };

  return (
    <div>
      {loading && <Alert alert={alertMsg} />}
      <div className="container" style={{ marginTop: "20px" }}>
        <form onSubmit={(event) => onSubmit(event)}>
          <div className="form-group">
            <label htmlFor="FromAcc">Your Account: </label>
            <input
              className="form-control"
              type="text"
              name="fromAcc"
              value={fromAcc}
              required
              onChange={(event) => onChange(event)}
            />
          </div>
          <div className="form-group">
            <label htmlFor="confirmAcc">Confirm Your Account: </label>
            <input
              className="form-control"
              type="text"
              name="confirmAcc"
              value={confirmAcc}
              required
              onChange={(event) => onChange(event)}
            />
          </div>
          <div className="form-group">
            <label htmlFor="ToAcc">Beneficiary Account:</label>
            <input
              className="form-control"
              type="text"
              name="toAcc"
              value={toAcc}
              onChange={(event) => onChange(event)}
              required
            />
          </div>
          <div className="form-group">
            <label htmlFor="amount">Amount</label>
            <input
              className="form-control"
              type="text"
              name="amount"
              value={amount}
              onChange={(event) => onChange(event)}
              required
            />
          </div>
          <button type="submit" className="btn btn-primary">
            Transfer
          </button>
          <Link to="/" className="btn">
            <button className="btn btn-primary">Home</button>
          </Link>
        </form>
      </div>
    </div>
  );
};

export default Transfer;
