import React, { useState, useEffect } from "react";
import "./User.css";
import { BrowserRouter as Router, Switch, Route } from "react-router-dom";
import NavBar from "./components/layout/NavBar";
import UsersList from "./components/UsersList";
import User from "./components/User";
import About from "./components/pages/About";
import axios from "axios";
import Transfer from "./components/Transfer";
import Pagination from "./components/Pagination";
import HomePage from "./components/pages/HomePage";
import CreateUser from "./components/CreateUser";
import Alert from "./components/layout/Alert";
function App() {
  const [users, setUsers] = useState([]);
  const [currentPage, setCurrentPage] = useState(1);
  const [usersPerPage] = useState(10);
  const [alert, setAlert] = useState(null);
  const getUsers = async () => {
    const res = await axios.get("/user/");
    setUsers(res.data);
  };

  const createNewUser = async (name, accNumber, email, creditAmount) => {
    const data = {
      name,
      accNumber,
      email,
      creditAmount,
    };
    const res = await axios({
      method: "post",
      url: "/newUser/",
      data,
      headers: { "Content-Type": "application/json" },
    });
    console.log(res.data);
  };
  useEffect(() => {
    getUsers();
  }, [users]);

  //Get Current Users

  const indexOfLastUser = currentPage * usersPerPage;
  const indexOfFirstUser = indexOfLastUser - usersPerPage;
  const currUsers = users.slice(indexOfFirstUser, indexOfLastUser);
  //Set Current Page
  const paginate = (pageNumber) => {
    setCurrentPage(pageNumber);
  };
  const setMessageAlert = (message, effect) => {
    setAlert({ msg: message, type: effect });
    setTimeout(() => {
      setAlert(null);
    }, 3000);
  };
  const numbOfPages = Math.ceil(users.length / usersPerPage) > 1;
  return (
    <Router>
      <div className="App">
        <NavBar />
        <Switch>
          <Route exact path="/" component={HomePage} />
          <Route
            exact
            path="/createUser"
            render={(props) => (
              <>
                {alert !== null && <Alert alert={alert} />}

                <CreateUser
                  createNewUser={createNewUser}
                  setAlert={setMessageAlert}
                />
              </>
            )}
          />
          <Route
            exact
            path="/users"
            render={(props) => (
              <>
                <UsersList users={currUsers} />
                {numbOfPages && (
                  <Pagination
                    usersPerPage={usersPerPage}
                    totalUsers={users.length}
                    paginate={paginate}
                    currentPage={currentPage}
                  />
                )}
              </>
            )}
          />
          <Route exact path="/user" component={User} />
          <Route exact path="/about" component={About} />
          <Route
            exact
            path="/transfer"
            render={(props) => (
              <>
                {alert !== null && <Alert alert={alert} />}
                <Transfer setAlert={setMessageAlert} />
              </>
            )}
          />
        </Switch>
      </div>
    </Router>
  );
}

export default App;
